@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.banner {
    display: flex;
    flex-direction:row;
    align-items:center;
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    z-index:98;
    height: 10vh;
    background-color: rgba(10, 7, 0, 0.603);
    box-shadow: 0 -0.1vh 0.5vh white;
    margin-bottom: 0;

    &__title__container {
        width: 60%;
        color: rgb(255, 255, 255);
        display:flex;
        flex-direction:row;
        flex-wrap: wrap;
        align-items: center;
        z-index: 99;
        font-family:"iceland";
        text-shadow: 0 0 1vh black;
        & h1 {
            width: 100%;
            font-size: 6vh;

            padding: 0;
            margin: 0;
            @include mobile-only {
                font-size: 6vw;
            }
        }
        & h2 {
            font-size: 3vh;
            padding-left: 5vw;
            width: 100%;
            padding: 0;
            margin: 0;
            color: orange;
            @include mobile-only {
                font-size: 5vw;
            }
        }
        @include mobile-only {
            font-size: 3vw;;
            flex-direction:row;
            width: 80vw;
        }
    }
    

    &__logo {
        height:20vh;
        border-radius: 10vh;
        margin-bottom: -10vh;
        margin-right: 2vw;
        margin-left: 3vw;
        box-shadow:2px  2px 8px rgb(2, 2, 2);
        z-index: 99;
        @include mobile-only {
            height: 10vh;
            margin-bottom: 0;
        }
    }
}