@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";


@keyframes enter-left-card {
    0% {
        left: -50vw;
    }
    100% {
        left: 0vw;
    }
}

@keyframes enter-right-card {
    0% {
        right: -50vw;
    }
    100% {
        right: 0vw;
    }
}

.title-card {
    position: absolute;
    max-height: 30vh;
    width: 50%;
    bottom: 0;
    overflow:hidden;
    display:flex;
    flex-direction: row;
    align-content:center;
    justify-content:space-between;
    align-items: flex-start;
    z-index:98;
    flex-wrap:wrap;
    @include mobile-only {
        height: 15vh;
    }
    &--enter-left {
        animation: 1s ease-in forwards enter-left-card;
    }
    &--enter-right {
        animation: 1s ease-in forwards enter-right-card;
    }
    &__title {
        color: hsl(204, 100%, 41%);
        background-color: rgba(0, 0, 0, 0.692);
        font-size: 7vh;
        width: 100%;
        text-align:center;
        height: 9vh;
        width: 100%;
        font-family:"iceland";
        font-weight:bold;
        background-image: url("/connectwork.png");
        display:flex;
        flex-direction: row;
        align-items: center;
        justify-content:center;
        @include mobile-only {
            font-size: 3vh;
            height: 4vh;
        }
        &:hover {
            color:orange;
        }
    }
    &__catchphrase {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        color: rgb(255, 94, 0);
        font-size: 2vw;
        width: 100%;
        text-align:center;
        height: 10vh;
        font-family: "Cabin sketch";
        /*background-image: url("/stripes-light.png");*/
        background-image: url("/dot-grid.png");
        font-family:"iceland";
        @include mobile-only {
            font-size: 2.5vh;
            height: 5vh;
            padding: 3vh;
        }
    }
}