@keyframes enter-bottom-desktop {
    0% {
        bottom: -20vh;
    }
    100% {
        bottom: 18.5vh;
    }
}

@keyframes enter-bottom-mobile {
    0% {
        bottom: -20vh;
    }
    100% {
        bottom: 14.8vh;
    }
}

@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.fluent {
    color: rgb(255, 255, 255);
    background-color:rgba(255, 166, 0, 0.74);
    font-size: 1.5vw;
    /*border: 2px solid black;*/
    width: 20vw;
    z-index: 98;
    font-family: "Gloria Hallelujah";
    text-align:center;
    text-shadow: 0 0 1vh black;
    position: absolute;
    animation: 1s 1s ease-in forwards enter-bottom-desktop;
    right:0;
    box-shadow: -1px -1px 3px white;
    
    
    @include mobile-only {
        font-size: 2vh;
        width: 50vw;
        animation: 1s 1s ease-in forwards enter-bottom-mobile;
        right:unset;
        left: 25vw;
        font-weight:bold;
    }

}