@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.location {
    width: 100%;
    display: flex;
    justify-content:center;
    flex-direction:row;
    flex-wrap:wrap;
    background-image: url("/dot-grid.png");
    & img{
        width: 100%;
        height: 50vh;
        object-fit: cover;
        clip-path: polygon(100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
    } 
    & i {
        font-size: 5vw;
        width: 8vw;
        height: 8vw;
        color: rgb(255, 0, 43);
        background-color:white;
        border: 1px solid red;
        border-radius: 50%;
        display:flex;
        flex-direction: row;
        justify-content:center;
        align-items:center;
        margin-bottom: -4vw;
        z-index:99;

        @include mobile-only {
            font-size: 15vw;
            width: 25vw;
            height: 25vw;
            margin-bottom: -12.5vw;
        }
    }
    &__text {
        width: 100%;
        height:max-content;
        text-align: center;
        font-size:1.3vw;
        height: auto;
        font-family: "iceland";
        padding: 2vh;
        box-sizing:border-box;
        color: rgb(0, 132, 255);
        @include mobile-only {
            font-size: 5vw;
        }
    }
}