@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.footer {
    width: 100vw;
    height: auto;
    z-index:99;
    color:white;
    font-family: "Gloria Hallelujah";
    display:flex;
    align-content: center;
    justify-content:space-evenly;
    flex-wrap: wrap;
    background-image: url("/connectwork.png");
    font-family:"iceland";
    & a {
        height: 5vh;
        align-items: center;
        text-align: left;
        text-decoration:none;
        color:rgb(0, 151, 197);
        margin: 5% 0 5% 5%;
        &:visited {
            color:rgb(0, 151, 197);
        }
    }
    &__information-container {
        display: flex;
        flex-direction:row;
        flex-wrap: nowrap;
        align-items:center;
        width: 100%;
        @include mobile-only {
            width:80vw;
            font-size: 4vw;
            
        }
        
        & img {
            width: 5vw;
            height:5vw;
        }
        & p {
            height: 5vh;
            margin-left: 5%;

        }
        & i {
            height: 5vh;
            width: 10%;

        }
        
        &--full-width {
            /*width: 100vw;*/
            justify-content: center;
            height: auto;
            flex-direction:row;
            border-top: 2px solid rgb(0, 151, 197);
            padding: 3vw 0 3vw 0;
            & a {
                text-align:center;
                width: 40vw;
            }
            @include mobile-only {
                padding: 0;
                width: 100%;
                margin:2vh;
            }
            & img {
                width: 5vw;
                height:5vw;
                @include mobile-only {
                    width: 10vw;
                    height:10vw;
                }
            }
            & p {
                height: auto;
                width: 27vw;
                align-items: center;
                text-align: center;
            }
            & i {
                height: auto;
                width: 40vw;
                align-items: center;
                text-align: center;
            }
           
        }
    }
    &__informations-container {
        width: 20vw;
        height: auto;
        font-size: 1.2vw;
        display: flex;
        flex-direction: column;
        
        justify-content:space-around;
        align-items: center;
        @include mobile-only {
            width:100vw;
            font-size: 4vw;
        }
        
           
        
        & img {
            width: 10vw;
            @include mobile-only {
                width:40vw;
                margin-bottom: 5vw;
                margin-left: -15vw;

            }
        }
        
    }
}