@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.contact-form {
    width: 100vw;
    height: auto;
    z-index:99;
    color:rgb(13, 126, 255);
    font-family: "Gloria Hallelujah";
    display:flex;
    flex-direction: row;
    align-content: center;
    justify-content:center;
    flex-wrap: wrap;
    background-image: url("/stripes-light.png");
    font-family:"iceland";
    padding: 0 0 2vw 0;

    &__error {
        display:block;
        color:rgba(255, 0, 0, 0.719);
        width: 60vw;
        text-align:right;
        @include mobile-only {
            text-align:center;
        }
        &--hidden {
            display:none;
        }
    }

    &__form {
        display:flex;
        flex-direction: column;
        align-items: center;
        justify-content:center;
        flex-wrap: wrap;
        background-color:rgba(236, 236, 236, 0.561);
        padding: 1vw 3vw;
        margin: 1vh 1vw;
        &--hidden {
            display:none;
        }
    }

    &__sent-message {
        display:none;
        
        &--visible {
            display:flex;
            flex-direction: column;
            align-items: center;
            justify-content:center;
            flex-wrap: wrap;
            background-color:rgba(236, 236, 236, 0.561);
            padding: 1vw 3vw;
            margin: 1vh 1vw;
            & i {
                font-size: 20vh;
            }
            & p {
                font-size: 10vh;
            }
        }
    }
    
    &__field-container {
        display:flex;
        flex-direction:row;
        font-size: 3vh;
        width:60vw;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 0 0 1vw 0;
        @include mobile-only {
            flex-direction: column;
            width: 70vw;
            height: auto;
            font-size: 4vw;
            align-items: center;
            margin: 0 0 3vh 0;
        }
    }
    &__title {
        font-size: 3vw;
        text-align: center;
        margin-bottom: 2vw;
        @include mobile-only {
            font-size: 3vh;
            margin-top: 2vh;
            margin-bottom: 2vh;
         }
    }
    & input {
        width: 40vw;
        @include mobile-only {
           width: 70vw;
        }
    }
    & textarea {
        width: 40vw;
        @include mobile-only {
           width: 70vw;
        }
    }
    & button {
        color: orange;
        border: 0.4vh solid orange;
        box-shadow: 0.2vw 0.2vw 0.3vw black;
        background-color:rgba(255, 255, 255, 0.753);
        width: 5vw;
        height:5vw;
        font-size: 4vh;
        border-radius: 3.5vw;
        decoration:none;
        outline:none;
        margin: 2vh 0;
        @include mobile-only {
           width: 15vw;
           height:15vw;
           border-radius: 15vw;
        }
    }
    &__radio-container {
        width:40vw;
        font-size: 1vw;
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-evenly;
        margin: 0 0 2vw 0;
        @include mobile-only {
            width: 70vw;
            margin: 2vh 0 0 0;

        }
        & input {
            width: 7%;
            height: 3vw;
            @include mobile-only {
                height: 3vh;
            }
        }
        & label {
            font-size: 1.5vw;
            width: 90%;
            text-align:center;
            height: 3vw;
            padding-top: 0.5fvw;
            @include mobile-only {
                font-size: 2.5vh;
            }
        }
    }
}