@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

html {

}

body {
    /*min-height: 100vh;*/
    /*height: -webkit-fill-available;*/
    justify-content:center;
    align-content:center;
    overflow-x:hidden;
    margin:0;
    padding:0;

}

#root {
    display:flex;
    flex-direction:row;
    align-content:center;
    justify-content:space-around;
    flex-wrap:wrap;
    width: 100%;
    overflow-x:hidden;
    padding: 0;
    margin: 0;
}

.card {
    display:flex;
    flex-direction:row;
    align-content:center;
    justify-content: space-around;
    align-items:center;
    margin-bottom:3vw;
    &--small {
        width:20vw;
        flex-direction:column;
    }
    &--reverse {
        flex-direction: row-reverse;
    }

}

.carousel {
    height:100vh;
    width: 100vw;
    background-image: url("/dot-grid.png");
    overflow:hidden;
}
a {
    text-decoration: none;
    outline: none
};