@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.faq {
    display:flex;
    flex-direction: column;
    justify-content:flex-start;
    width: 100%;
    padding: 0 0 5vh 0;
    height: max-content;
    &__element {
        width: 100%;
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        font-family: "iceland";
        margin:0 0;
        height: max-content;
        &__title-container {
            display:flex;
            flex-direction:row;
            width:100%;
            height: auto;
            align-items:center;
            justify-content: left;
            /*background: rgba(255, 255, 255, 0.561);*/
            color: orange;
            padding: 1vw 0 1vw 10vw;
            @include mobile-only {
                padding: 1vw 0 1vw 3vw;
            }

                       
            & > * {
                font-size: 1.8vw;
                height: auto;
                display:flex;
                align-items:center;
                margin: 0;
                padding:0;
                @include mobile-only {
                    font-size: 3vh;
                }
            }
        }
        &__title {
            text-align:left;
            width: 80%;
            padding-left: 20vw;
            @include mobile-only {
                padding-left: 1vw;
                width: 90%;
            }
        }
        & i {
            width: 10%;
        }
        &__content {
            display:none;
            &--visible {
                display:block;
                text-align: center;
                width: 80vw;
                text-align: justify;
                font-size: 1.3vw;
                padding-left: 10vw;
                @include mobile-only {
                    font-size: 3vh;
                }
            }

        }
    }
}