@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.buy-now {
    width: 100%;
    background-image: url("/connectwork.png");
    justify-content:space-around;
    display:flex;
    flex-wrap:wrap;
    flex-direction:row;
    justify-items:center;
    justify-content:center;

    &__buy {
        width: 100%;
        text-align:center;
    }
    &__button-container {
        display:flex;
        flex-direction:row;
        justify-content:center;
        flex-wrap:wrap;
        width: 100%;
        @include mobile-only {
            width: 80vw;
            padding: 0 10vw;
        }
    }
    & h2 {
        font-size: 3vh;
        width: 100%;
        text-align:center;
        padding: 0;
        margin: 0;
        color: rgb(0, 153, 255);
        font-family:"iceland";
        @include mobile-only {
            font-size: 5vw;
        }
    }
    & a {   
        font-size: 2vw;
        width: auto;
        height: auto;
        background: url("/stripes-light.png");

        margin: 3vh;
        color: rgb(255, 0, 0);
        font-family: "Gloria Hallelujah";
        border-radius: 1vh;
        border: 1px solid  rgb(255, 0, 0);
        display:flex;
        flex-direction:row;
        flex-wrap:wrap;
        justify-content: center;
        @include mobile-only {
            font-size: 5vw;
        }

    }
    & img {
        height: 7vh;
    }
    & i {
        color: rgb(0, 173, 9);
        background-color: rgb(255, 255, 255);
        padding: 1vw;
        border-radius: 50%;
        margin-bottom: 1vw;
        width: auto;
        height: auto;
    }
}